import React, { useEffect } from 'react';

import '../css/kakao.css';

const KakaoShare = (props) => {
  const url = window.location.href; // url
  useEffect(() => {
    initKakao(); //
  }, []);

  const initKakao = () => {
    if (window.Kakao) {
      const kakao = window.Kakao;
      if (!kakao.isInitialized()) {
        kakao.init('d237771033c5a85896cf9299be47f483');
      }
    }
  };

  const shareKakao = () => {
    window.Kakao.Link.sendDefault({
      objectType: 'feed',
      content: {
        title: '  Wordle',
        description: ` ${props.score} !      ?`,
        imageUrl: 'https://ifh.cc/g/kOKxN5.png',
        link: {
          mobileWebUrl: url,
          webUrl: url,
        },
      },
      buttons: [
        {
          title: ' ',
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
      ],
    });
  };

  return (
    <div className="share-node" onClick={shareKakao}>
      <img
        src="/public_assets/kakao_share_btn.png"
        alt=""
        className="kakao_share_btn"
      />
      <p className="kakao_share_btn_title"> </p>
    </div>
  );
};

export default KakaoShare;
