import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { checkCookie, clearCookie } from '../util/cookie.js';

import '../css/rules.css';

const Rules = (props) => {
  const [isLogin, setIsLogin] = useState(false);
  const kakaoLogin = async () => {
    const url = 'http://localhost:4000/api/auth/kakao';
    await axios.get(url);
  };

  useEffect(() => {
    (async function () {
      const user = await checkCookie();
      if (!user) {
        return;
      }
      setIsLogin(user);
    })();
  }, []);

  const startClick = () => {
    props.startClick(isLogin.u_id);
  };

  const logoutClick = () => {
    clearCookie();
    setIsLogin(false);
  };

  return (
    <ul className="rules">
      <li>
        <h1> </h1>
      </li>
      <li>
        <h3>1.  5 .(    )</h3>
      </li>
      <li>
        <h3>
          2.           .
        </h3>
      </li>
      <li>
        <h3>3.     3 .</h3>
        <ul>
          <li> :    </li>
          <li> :     </li>
          <li> :    </li>
        </ul>
      </li>
      {isLogin ? (
        <>
          <p> {isLogin.userAlias}  </p>
          <p> {isLogin.userAlias} </p>
          {isLogin.bestScore > 0 ? (
            <p>
              <span className="bestScore">{isLogin.bestScore}</span> 
              !
            </p>
          ) : (
            <p>  !   !</p>
          )}
          <div className="btn_container">
            <button onClick={logoutClick} className="logoutBtn">
              
            </button>
            <button onClick={startClick} className="startBtn">
              Game Start
            </button>
          </div>
        </>
      ) : (
        <a href="http://localhost:4000/api/auth/kakao">
          <img
            src="/public_assets/kakao_login.png"
            alt=""
            className="kakao_btn"
            onClick={kakaoLogin}
          />
        </a>
      )}
      <p className="dev_info">
        Developer : <a href="https://github.com/green-kong">Github</a>{' '}
        <a href="https://kong-dev.tistory.com/">Blog</a>
      </p>
    </ul>
  );
};

export default Rules;
